@import "variables";
@import "mixins";
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
a img           {border:none;vertical-align: middle;}
input, textarea {outline:none; resize:none;}
.clear           {clear:both; line-height:0; height:0; display:block;}
.fl_l, .pull-left{ float:left!important;}
.fl_r, .pull-right{ float:right!important;}
.text_r         {text-align: right;}
.text_c         {text-align: center;}
.text_l         {text-align: left;}
.ov             {overflow: hidden;}
.no_ov          {overflow: visible!important;}
.no_bg          {background: none!important;}
.m_0            {margin: 0!important;}
.block          {display: block;}
.in_bl          {display: inline-block !important;}
.inline         {display: inline-block;}
.hidden         {visibility: hidden;}
.none           {display: none!important;}
sup, sub        {vertical-align: baseline;position: relative;font-size: .45rem;line-height: 1;}
sup             {bottom: 1.3ex;}
sub             {top: .5ex;}
.va_t           {vertical-align: top;}
.va_m           {vertical-align: middle;}
.va_b           {vertical-align: bottom;}
.va_middle_out  {position: relative;line-height: 0;font-size: 0;}
.nowrap         {white-space: nowrap;}
.del            {color: rgba(255,98,104, 0.5);}
.nonMember      {color: rgba(0,0,0,0.7);}
.relative       {position: relative;}
