//clearfix
.clearfix{
  &:before,
  &:after{
    content: " ";display: table;
  }
  &:after{
    clear: both;
  }
}
.table_clearfix{
  display: table;
  &:after{
	content: '. .';
	display: block;
	word-spacing: 99in;
	height: 0;
	overflow: hidden;
	font-size: 0.13em;
	line-height: 0;
  }
}
//.va_middle_out
.va_middle_out  {
  position: relative;
  line-height: 0;
  font-size: 0;
  &:after{
	position: relative;
	content: "";
	height: 100%;
	width: 0;
	display: inline-block;
	vertical-align: middle;
  }
}
.va_middle_in{
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  width: 100%;
  text-decoration: none;
}
//justified
.justified{
  line-height: 0;
  font-size: 0;
  text-align: justify;
  text-align-last: justify;
  &:after{
	width: 100%;
	height: 0;
	visibility: hidden;
	overflow: hidden;
	content: '';
	display: inline-block;
  }
}

.fade{
	opacity: 0;
	-webkit-transition: opacity 0.15s linear;
	transition: opacity 0.15s linear;
}
.tab-content > .tab-pane {
	display: none;
}
.fade.in {
	opacity: 1;
}
.tab-content > .active {
	display: block;
}
